.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-container {
  background-color: white;
  border-radius: 0.75rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  border-bottom: 1px solid #e5e7eb;
}
.modal-header .modal-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}
.modal-header .close-btn {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 0.375rem;
  transition: all 0.2s;
}
.modal-header .close-btn:hover {
  background-color: #f3f4f6;
  color: #374151;
}
.modal-header .close-btn svg {
  width: 1.25rem;
  height: 1.25rem;
}

.modal-body {
  padding: 1.5rem;
}
.modal-body .form-section {
  margin-bottom: 2rem;
}
.modal-body .form-section:last-child {
  margin-bottom: 0;
}
.modal-body .form-section .section-header {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  gap: 0.75rem;
}
.modal-body .form-section .section-header .section-icon {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-body .form-section .section-header .section-icon.supplier {
  background-color: #dbeafe;
  color: #1d4ed8;
}
.modal-body .form-section .section-header .section-icon.order {
  background-color: #dcfce7;
  color: #16a34a;
}
.modal-body .form-section .section-header .section-icon.notes {
  background-color: #fef3c7;
  color: #d97706;
}
.modal-body .form-section .section-header .section-icon.items {
  background-color: #e0e7ff;
  color: #7c3aed;
}
.modal-body .form-section .section-header .section-icon svg {
  width: 1.25rem;
  height: 1.25rem;
}
.modal-body .form-section .section-header .section-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0;
  flex-grow: 1;
}
.modal-body .form-section .section-header .btn {
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: 1px solid #d1d5db;
  background-color: white;
  color: #374151;
}
.modal-body .form-section .section-header .btn:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.modal-body .form-section .section-header .btn svg {
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
}
.modal-body .form-section .form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}
.modal-body .form-section .form-row:last-child {
  margin-bottom: 0;
}
.modal-body .form-section .form-row .form-field.full-width {
  grid-column: 1/-1;
}
.modal-body .form-section .form-field .field-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}
.modal-body .form-section .form-field .form-input,
.modal-body .form-section .form-field .form-select,
.modal-body .form-section .form-field .form-textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  transition: border-color 0.2s;
}
.modal-body .form-section .form-field .form-input:focus,
.modal-body .form-section .form-field .form-select:focus,
.modal-body .form-section .form-field .form-textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.modal-body .form-section .form-field .form-input.ng-invalid.ng-touched,
.modal-body .form-section .form-field .form-select.ng-invalid.ng-touched,
.modal-body .form-section .form-field .form-textarea.ng-invalid.ng-touched {
  border-color: #ef4444;
}
.modal-body .form-section .form-field .form-input.flatpickr-input,
.modal-body .form-section .form-field .form-select.flatpickr-input,
.modal-body .form-section .form-field .form-textarea.flatpickr-input {
  cursor: pointer;
  background-color: #ffffff;
}
.modal-body .form-section .form-field .form-input.flatpickr-input:hover,
.modal-body .form-section .form-field .form-select.flatpickr-input:hover,
.modal-body .form-section .form-field .form-textarea.flatpickr-input:hover {
  border-color: #9ca3af;
}
.modal-body .form-section .form-field .form-textarea {
  resize: vertical;
  min-height: 80px;
}
.modal-body .form-section .form-field .error-message {
  color: #ef4444;
  font-size: 0.75rem;
  margin-top: 0.25rem;
  min-height: 1rem;
}
.modal-body .form-section .items-container .item-row {
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #f9fafb;
}
.modal-body .form-section .items-container .item-row:last-child {
  margin-bottom: 0;
}
.modal-body .form-section .items-container .item-row .item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.modal-body .form-section .items-container .item-row .item-header .item-title {
  font-size: 1rem;
  font-weight: 600;
  color: #374151;
  margin: 0;
}
.modal-body .form-section .items-container .item-row .item-header .btn-danger {
  background-color: #fef2f2;
  color: #dc2626;
  border-color: #fecaca;
}
.modal-body .form-section .items-container .item-row .item-header .btn-danger:hover {
  background-color: #fee2e2;
  border-color: #fca5a5;
}
.modal-body .form-section .items-container .item-row .form-row {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 0.75rem;
}
.modal-body .form-section .items-container .item-row .form-row .line-total {
  padding: 0.75rem;
  background-color: #f3f4f6;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-weight: 600;
  color: #111827;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-body .form-section .total-section {
  margin-top: 1.5rem;
  padding: 1rem;
  background-color: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
}
.modal-body .form-section .total-section .total-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid #e2e8f0;
}
.modal-body .form-section .total-section .total-row:last-child {
  border-bottom: none;
}
.modal-body .form-section .total-section .total-row.total-final {
  font-weight: 600;
  font-size: 1.1rem;
  color: #1e293b;
  border-top: 2px solid #3b82f6;
  padding-top: 0.75rem;
  margin-top: 0.5rem;
}
.modal-body .form-section .total-section .total-label {
  font-weight: 500;
  color: #475569;
}
.modal-body .form-section .total-section .total-value {
  font-weight: 600;
  color: #1e293b;
}

.supplier-selection {
  display: flex;
  gap: 0.75rem;
  align-items: flex-end;
}
.supplier-selection .form-select {
  flex: 1;
}
.supplier-selection .btn {
  white-space: nowrap;
  padding: 0.75rem 1rem;
  font-size: 0.875rem;
  border: 1px solid #3b82f6;
  background-color: #3b82f6;
  color: #ffffff;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
}
.supplier-selection .btn:hover {
  background-color: #2563eb;
  border-color: #2563eb;
  color: #ffffff;
}
.supplier-selection .btn.btn-outline {
  border: 1px solid #3b82f6;
  background-color: #3b82f6;
  color: #ffffff;
}
.supplier-selection .btn.btn-outline:hover {
  background-color: #2563eb;
  border-color: #2563eb;
  color: #ffffff;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem 1.5rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
}
.modal-footer .btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}
.modal-footer .btn.btn-secondary {
  background-color: white;
  color: #374151;
  border: 1px solid #d1d5db;
}
.modal-footer .btn.btn-secondary:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.modal-footer .btn.btn-primary {
  background-color: #3b82f6;
  color: white;
}
.modal-footer .btn.btn-primary:hover {
  background-color: #2563eb;
}
.modal-footer .btn.btn-primary:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

@media (max-width: 640px) {
  .modal-container {
    margin: 0.5rem;
    max-height: calc(100vh - 1rem);
  }
  .modal-body {
    padding: 1rem;
  }
  .modal-body .form-section .form-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  .modal-body .form-section .items-container .item-row .form-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  .modal-footer {
    flex-direction: column;
    gap: 0.75rem;
  }
  .modal-footer .btn {
    width: 100%;
  }
}
@media (max-width: 480px) {
  .modal-header {
    padding: 1rem 1rem 0.75rem 1rem;
  }
  .modal-header .modal-title {
    font-size: 1.125rem;
  }
  .modal-body {
    padding: 0.75rem;
  }
  .modal-footer {
    padding: 0.75rem 1rem 1rem 1rem;
  }
}
.form-hint {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem;
  background-color: #f0f9ff;
  border: 1px solid #bae6fd;
  border-radius: 0.5rem;
  margin: 0.75rem 0;
  font-size: 0.875rem;
  color: #0369a1;
}
.form-hint svg {
  flex-shrink: 0;
  color: #0284c7;
}
.form-hint span {
  line-height: 1.4;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.animate-spin {
  animation: spin 1s linear infinite;
}
